
import { FiMail, FiPhone, FiMapPin, FiUser, FiSend } from "react-icons/fi";
import "./contact.css";
import React from "react";
import { Reveal } from "../reveal/Reveal";

import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";
import Footer from "../footer/Footer";
import Header from "../header/Header";


// === Hero Section Component ===
const ContactHeroSection: React.FC<{ hero: typeof contactHeroContent }> = ({ hero }) => (
  <section className="contact-hero">
    <img
      src={hero.bgImage}
      alt="Contact Tinh Hoa Việt"
      className="contact-hero__bg"
      loading="eager"
    />
    <div className="contact-hero__overlay" />
    <div className="contact-hero__inner">
      <div className="contact-hero__content">
        <p className="contact-hero__eyebrow">{hero.eyebrow}</p>
        <h1 className="contact-hero__title">
          <span className="contact-hero__title-main">{hero.titleMain}</span>
          <span className="contact-hero__title-sub">{hero.titleSub}</span>
        </h1>
        <p className="contact-hero__desc">{hero.description}</p>
      </div>
    </div>
  </section>
);

// === Info Cards Component ===
const ContactInfoCards: React.FC<{ cards: typeof contactInfoCards }> = ({ cards }) => (
  <Reveal direction="up" delay={200}>
    <div className="contact-info-grid">
      {cards.map((card: any, i: number) => (
        <div key={i} className="contact-info-card">
          <div className="contact-icon">{card.icon}</div>
          <h3>{card.title}</h3>
          {card.lines.map((line: any, idx: number) => (
            <p key={idx}>{line}</p>
          ))}
        </div>
      ))}
    </div>
  </Reveal>
);

// === Form Component ===
const ContactForm: React.FC<{
  header: typeof contactFormContent.header;
  fields: typeof contactFormContent.fields;
  submitButton: typeof contactFormContent.submitButton;
  note: string;
}> = ({ header, fields, submitButton, note }) => (
  <Reveal direction="up" delay={400}>
    <div className="contact-form-wrap">
      <div className="contact-form-bg" />
      <form className="contact-form" onSubmit={(e) => e.preventDefault()}>
        <div className="contact-form-header">
          <h2>{header.title}</h2>
          <p>{header.description}</p>
        </div>

        <div className="contact-form-grid">
          {fields.map((field: any, i: number) => {
            if (field.type === "select") {
              return (
                <div key={i} className="contact-field floating">
                  <select
                    id={field.id}
                    name={field.name}
                    required={field.required}
                    defaultValue=""
                  >
                    <option value="" disabled hidden />
                    {field.options?.map((opt: string, idx: number) => (
                      <option key={idx}>{opt}</option>
                    ))}
                  </select>
                  <label htmlFor={field.id}>{field.label}</label>
                </div>
              );
            }

            if (field.type === "textarea") {
              return (
                <div key={i} className="contact-field floating span-2">
                  <textarea
                    id={field.id}
                    name={field.name}
                    required={field.required}
                    rows={field.rows}
                    placeholder={field.placeholder}
                  />
                  <label htmlFor={field.id}>{field.label}</label>
                </div>
              );
            }

            return (
              <div key={i} className="contact-field floating">
                {field.icon}
                <input
                  id={field.id}
                  name={field.name}
                  type={field.type}
                  required={field.required}
                  placeholder={field.placeholder}
                />
                <label htmlFor={field.id}>{field.label}</label>
              </div>
            );
          })}
        </div>

        <div className="contact-form-actions">
          <button className="contact-btn-primary" type="submit">
            {submitButton.icon}
            {submitButton.text}
          </button>
        </div>

        <p className="contact-form-note">{note}</p>
      </form>
    </div>
  </Reveal>
);

// === Main Page Component ===
const Contact: React.FC = () => {
  const { lang } = useLanguage();
  const contactContent = getContent(lang).pages.contact?.section || {
    contactHeroContent: {
      bgImage: "https://images.unsplash.com/photo-1486406146926-c627a92ad1ab?q=80&w=1920&auto=format&fit=crop",
      eyebrow: lang === "en" ? "Get In Touch" : "Liên hệ",
      titleMain: lang === "en" ? "Contact Us" : "Liên hệ chúng tôi",
      titleSub: lang === "en" ? "Connect with Vietnam's Quintessence Verification Center" : "Kết nối với Trung tâm Thẩm định Tinh hoa Việt Nam",
      description: lang === "en" ? "Have questions about our verification process, want to submit a nomination, or need assistance? We're here to help you discover and promote Vietnam's finest quintessence." : "Bạn có câu hỏi về quy trình thẩm định, muốn gửi đề cử hoặc cần hỗ trợ? Chúng tôi sẵn sàng giúp bạn khám phá và quảng bá tinh hoa Việt Nam."
    },
    contactInfoCards: [
      {
        icon: <FiMail />,
        title: lang === "en" ? "Email Us" : "Gửi email",
        lines: ["info@tinhhoaviet.com", "nominations@tinhhoaviet.com"]
      },
      {
        icon: <FiPhone />,
        title: lang === "en" ? "Call Us" : "Gọi điện thoại",
        lines: ["+84 (0) 123 456 789", "+84 (0) 987 654 321"]
      },
      {
        icon: <FiMapPin />,
        title: lang === "en" ? "Visit Us" : "Ghé thăm",
        lines: [lang === "en" ? "123 Nguyen Hue Boulevard" : "123 Đại lộ Nguyễn Huệ", lang === "en" ? "District 1, Ho Chi Minh City" : "Quận 1, Thành phố Hồ Chí Minh"]
      }
    ],
    contactFormContent: {
      header: {
        title: lang === "en" ? "Send us a Message" : "Gửi tin nhắn cho chúng tôi",
        description: lang === "en" ? "We'd love to hear from you. Send us a message and we'll respond as soon as possible." : "Chúng tôi rất muốn nghe từ bạn. Gửi tin nhắn và chúng tôi sẽ phản hồi sớm nhất có thể."
      },
      fields: [
        {
          id: "name",
          name: "name",
          type: "text",
          required: true,
          placeholder: " ",
          label: lang === "en" ? "Full Name *" : "Họ và tên *",
          icon: <FiUser className="field-icon" />
        },
        {
          id: "email",
          name: "email",
          type: "email",
          required: true,
          placeholder: " ",
          label: lang === "en" ? "Email Address *" : "Địa chỉ email *",
          icon: <FiMail className="field-icon" />
        },
        {
          id: "phone",
          name: "phone",
          type: "tel",
          required: false,
          placeholder: " ",
          label: lang === "en" ? "Phone Number" : "Số điện thoại",
          icon: <FiPhone className="field-icon" />
        },
        {
          id: "subject",
          name: "subject",
          type: "select",
          required: true,
          options: lang === "en" ? [
            "General Inquiry",
            "Nomination Submission",
            "Verification Process",
            "Partnership Opportunity",
            "Media & Press",
            "Technical Support"
          ] : [
            "Thắc mắc chung",
            "Gửi đề cử",
            "Quy trình thẩm định",
            "Cơ hội hợp tác",
            "Truyền thông & Báo chí",
            "Hỗ trợ kỹ thuật"
          ],
          label: lang === "en" ? "Subject *" : "Chủ đề *"
        },
        {
          id: "message",
          name: "message",
          type: "textarea",
          required: true,
          rows: 6,
          placeholder: " ",
          label: lang === "en" ? "Your Message *" : "Tin nhắn của bạn *"
        }
      ],
      submitButton: {
        icon: <FiSend />,
        text: lang === "en" ? "Send Message" : "Gửi tin nhắn"
      },
      note: lang === "en" ? "By submitting this form, you agree to our privacy policy and terms of service." : "Bằng cách gửi biểu mẫu này, bạn đồng ý với chính sách bảo mật và điều khoản dịch vụ của chúng tôi."
    }
  };
  
  return (
    <div>
      <Header />
      <ContactHeroSection hero={contactContent.contactHeroContent} />
      <section className="contact-section">
        <div className="contact-container">
          <ContactInfoCards cards={contactContent.contactInfoCards} />
          <ContactForm
            header={contactContent.contactFormContent.header}
            fields={contactContent.contactFormContent.fields}
            submitButton={contactContent.contactFormContent.submitButton}
            note={contactContent.contactFormContent.note}
          />
        </div>
      </section>
      <Footer />
    </div>
  );
};

export default Contact;